





























































import { Component, Vue, Watch } from 'vue-property-decorator';
import SwordIcon from 'vue-material-design-icons/Sword.vue';
import HeartIcon from 'vue-material-design-icons/Heart.vue';
import 'vue-material-design-icons/styles.css';
import { Card } from "@/types";

interface HTMLInputEvent extends Event {
    target: HTMLInputElement & EventTarget;
}

@Component({
    components: {
        SwordIcon,
        HeartIcon
    }
})
export default class Home extends Vue {
    private cardData: Partial<Card> = {};
    private fontSize = 12;
    /* eslint-disable */

    mounted() {
        const storageVal = localStorage.getItem("cardData");
        if (storageVal) {
            this.cardData = JSON.parse(storageVal);
        }
    }

    fixFontSize(): void {
        const canvas = document.createElement("canvas");
        const context = canvas.getContext("2d")!;
        context.font = `15px RobotoSlab`;
        const metrics = context.measureText(`"${this.cardData.quote}"`);
        const totalWidth = metrics.width;
        const lineCount = Math.ceil(totalWidth / 180);

        const newFontSize = Math.ceil(60 / 1.15 / lineCount);
        if (newFontSize <= 12) {
            this.fontSize = newFontSize;
        }
    }

    uploadImage(e: HTMLInputEvent): void {
        const file = e.target?.files?.[0];
        if (!file) {
            return;
        }
        const objectUrl = window.URL.createObjectURL(file);
        this.cardData = { ...this.cardData, imageURL: objectUrl };
        console.log(document.defaultView!.getComputedStyle(document.getElementById("cardQuote")!, null).getPropertyValue("lineHeight"));
    }

    @Watch("cardData", { deep: true })
    onPropertyChanged() {
        localStorage.setItem("cardData", JSON.stringify(this.cardData));
    }
}
